* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.btn-accordion{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;

    width: 60vw;
    height: 6vw;
    border: none;
    cursor: pointer;

    transition: all 0.6s ease;
    background-color: #2f3947;
    box-shadow: 1px 1px 5px rgba(27, 27, 27, 0.671);

    margin: 10px;
}



.accordionSection {
    display:flex ;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 5px;
    padding-right: 10px;
}

.btn:hover{
    background-color: #5e5e5f;
    box-shadow: 1px 1px 10px rgba(27, 27, 27, 0.671);
}

.accordionContent  {
    overflow: hidden;
    transition: all 0.5s ease;
}

.accordionText{
    padding: 20px;
}

.active{
    background-color: #6d80a0 !important;
}

.triangle{
    margin-right: 1vw;
}

.accordionImage{
    width: 8vw;

}

.accordionTitle{
    align-self: center;
    margin-right: 3vw;
    color: white;
    font-weight: bold;
}

@media only screen and (max-width:600px){
    .btn{
        width: 80vw;
        border-radius: 12px;
    }
}